@import url("https://fonts.googleapis.com/css2?family=Hammersmith+One:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-hammersmith-one: "Hammersmith One";

  /* font sizes */
  --font-size-3xs: 10px;
  --font-size-5xs: 8px;
  --font-size-xl: 20px;
  --font-size-mini: 15px;
  --font-size-11xl: 30px;

  /* Colors */
  --color-white: #fff;
  --color-lightgray: #cccdd0;
  --color-gray-100: #fffdfd;
  --color-gray-200: #2a2a2b;
  --color-gainsboro: #e8e8ea;
  --color-black: #000;
  --color-dodgerblue: #008bff;
  --color-whitesmoke: #f5f7fa;

  /* Border radiuses */
  --br-3xs: 10px;
}
