.heroBackground {
  position: relative;
  width: 1440px;
  height: 311px;
  background-image: url(/public/herobackground@2x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  z-index: 0;
}
.heroMessage,
.heroTitle {
  position: absolute;
  margin: 0 !important;
  display: inline-block;
  flex-shrink: 0;
}
.heroTitle {
  top: 117px;
  left: 508px;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  width: 446px;
  height: 37px;
  z-index: 1;
}
.heroMessage {
  top: 199px;
  left: 530px;
  font-size: var(--font-size-xl);
  text-align: center;
  width: 354px;
  height: 64px;
  z-index: 2;
}
.hero {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 10px;
}
.demoHomePage {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  height: 1024px;
  overflow: hidden;
  text-align: left;
  font-size: 40px;
  color: var(--color-white);
  font-family: var(--font-hammersmith-one);
}
